import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import { GlobalValidate } from '../../../../../_services/validate.service';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./queue-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class QueueAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
 
queueForm: FormGroup;
ticketqueueId = 0;  
ajaxData : QueueData[];
queue = new QueueData(); 
config = new configDetails(); 
status;
bootstrapMsg:string;
ticketqueueduehrsmsg:string;
ticketqueuetrigger1hrsmsg:string;
ticketqueuestarttimemsg:string;
ticketqueueendtimemsg:string;
msg = [];
bootstrapFormValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute, private globalValid: GlobalValidate) {
	this.route.queryParams.filter(params => params.ticketqueueId).subscribe(params=>{
			if(params.ticketqueueId != undefined){
				this.ticketqueueId=params.ticketqueueId; 
			}
		}); 
	console.log("Group_ID=="+this.ticketqueueId);
	if(this.ticketqueueId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.queueForm = new FormGroup({			
			'ticketqueueName' : new FormControl('', [ Validators.required]),
			'ticketqueueDesc' : new FormControl('', [ Validators.required]),
			'ticketqueueBusinessHrs' : new FormControl(''),
			'ticketqueueSun' : new FormControl(''),
			'ticketqueueMon' : new FormControl(''),
			'ticketqueueTue' : new FormControl(''),
			'ticketqueueWed' : new FormControl(''),
			'ticketqueueThu' : new FormControl(''),
			'ticketqueueFri' : new FormControl(''),
			'ticketqueueSat' : new FormControl(''),
		
		});
	}
	  
	get ticketqueueName(){ return this.queueForm.get('ticketqueueName')};
	get ticketqueueDesc(){ return this.queueForm.get('ticketqueueDesc')};
	get ticketqueueBusinessHrs(){ return this.queueForm.get('ticketqueueBusinessHrs')};
	get ticketqueueSun(){ return this.queueForm.get('ticketqueueSun')};
	get ticketqueueMon(){ return this.queueForm.get('ticketqueueMon')};
	get ticketqueueTue(){ return this.queueForm.get('ticketqueueTue')};
	get ticketqueueWed(){ return this.queueForm.get('ticketqueueWed')};
	get ticketqueueThu(){ return this.queueForm.get('ticketqueueThu')};
	get ticketqueueFri(){ return this.queueForm.get('ticketqueueFri')};
	get ticketqueueSat(){ return this.queueForm.get('ticketqueueSat')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<QueueData>(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/ticketqueue-data ',
							 			JSON.stringify({ 'ticketqueueId' :this.ticketqueueId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.queue = data['populatadata'][0];
																		console.log("ticketqueueModifiedId=="+this.queue.ticketqueueModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
  ngAfterViewInit(){
	    $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){});
	   }	
	
	  
	 toggleAnimation(){
		this._router.navigate(['portal/customer-group-list']);      //path should be changed
	 }

  getCheckValues(temp:string){
	return this.globalValid.getCheckValue(temp);
	
	}
 
	  	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.queue[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
				if(this.bootstrapFormValues[i].name.includes('check_')){
					this.queue[this.bootstrapFormValues[i].name.split('_')[1]] = "1";
				}else{
					this.queue[this.bootstrapFormValues[i].name] = this.bootstrapFormValues[i].value;
				}
			}
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				console.log(this.bootstrapFormValues[i]);
				if(this.bootstrapFormValues[i].name=="ticketqueueDuehrs"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Ticket Due Hours In!<br>';
						this.ticketqueueduehrsmsg="<font class='errorMsg'>Enter Ticket Due Hours</font>";
						
					}else{
						this.ticketqueueduehrsmsg = "";
					}
				}
//				if(this.bootstrapFormValues[i].name=="ticketqueueTrigger1Hrs"){
//					if(this.bootstrapFormValues[i].value==""){
//						this.bootstrapMsg = this.bootstrapMsg +  'Enter Ticket Queue T!<br>';
//						this.ticketqueuetrigger1hrsmsg="<font class='errorMsg'>Enter Ticket Trigger1 Time</font>";
//						
//					}else{
//						this.ticketqueuetrigger1hrsmsg = "";
//					}
//				}
				if(this.bootstrapFormValues[i].name=="ticketqueueStarttime"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Ticket Start Time!<br>';
						this.ticketqueuestarttimemsg="<font class='errorMsg'>Enter Ticket Queue Start Time In</font>";
						
					}else{
						this.ticketqueuestarttimemsg = "";
					}
				}
				if(this.bootstrapFormValues[i].name=="ticketqueueEndtime"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Ticket End Time!<br>';
						this.ticketqueueendtimemsg="<font class='errorMsg'>Enter Ticket Queue End Time In</font>";
						
					}else{
						this.ticketqueueendtimemsg = "";
					}
				}
			}
			if(this.bootstrapMsg != ''){
				this.msg[0]='Error';
			 	this.msg[1]='Enter the Mandatory Fields' ;
				this.toast.showtoast(this.msg);
				this.bootstrapMsg='';
			}
			
			console.log(this.bootstrapMsg);
			if(!this.queueForm.invalid && this.bootstrapMsg==''){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.ticketqueueId == 0){ 
				this.queue['requestType'] = 'add';
			}else{
				this.queue['requestType'] = 'update';
				this.queue['ticketqueueId'] = this.ticketqueueId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/ticketqueue',  //path should be changed
									JSON.stringify(this.queue),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
	//			  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.queueForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.queue);
	  }
 
  onDelete(){
		 this.queue['requestType'] = 'delete';
		this.queue['ticketqueueId'] = this.ticketqueueId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/queue',//path should be changed
									JSON.stringify(this.queue),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
//			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.queueForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

export class QueueData {

    ticketqueueId: string;
    ticketqueueName: string;
	ticketqueueDesc: string;
	ticketqueueRank: string;
	ticketqueueDuehrs: string='';
	ticketqueueTrigger1Hrs: string='';
	ticketqueueTrigger2Hrs: string='';
	ticketqueueTrigger3Hrs: string='';
	ticketqueueTrigger4Hrs: string='';
	ticketqueueTrigger5Hrs: string='';
	ticketqueueBusinessHrs: string='1';
	ticketqueueStarttime: string='';
	ticketqueueEndtime: string='';
	ticketqueueSun: string;
	ticketqueueMon: string;
	ticketqueueTue: string;
	ticketqueueWed: string;
	ticketqueueThu: string;
	ticketqueueFri: string;
	ticketqueueSat: string;
	ticketqueueEntryId: string;
	ticketqueueEntryDate: string;
	ticketqueueModifiedId: string;
	ticketqueueModifiedDate: string;
  
}
export class configDetails {
	configCustomerSob:string;
	configCustomerSoe:string;
	configCustomerAddress:string;
}



